.des-container {
  background-color: var(--color-1);
}

.destination-port {
  display: flex;
  background-color: var(--color-1);
  color: white;

  .destination-row-inner-left {
    width: 100%;
    height: 100%;
    padding: 0.7em 1em;
    align-self: center;
    text-align: right;
  }

  .destination-row-inner-right {
    width: 100%;
    height: 100%;
    padding: 0 1em;
    align-self: center;
    text-align: left;
    color: var(--color-4);
    font-size: x-large;
  }

  .destination-active {
    text-decoration: underline;
    text-underline-position: under;
  }
}

.destination-port__loading {
  width: 100%;
  height: 1em;
  padding: 0 1em;
}
